import React from 'react';
import GatsbyImage from 'gatsby-image';
import styled, { css } from 'styled-components';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';

import SEO from '../components/Seo';

const containerStyles = ({ theme }) => css`
  ${theme.mq.huge`
    display: flex;
  `};
`;

const Container = styled('div')`
  ${containerStyles}
`;

const headerContainerStyles = ({ theme }) => css`
  ${theme.mq.huge`
     width: 30%;
     padding-right: 20px;
  `};
`;

const HeaderContainer = styled('div')`
  ${headerContainerStyles}
`;

const contentContainerStyles = ({ theme }) => css`
  ${theme.mq.huge`
    width: 70%;
  `};
`;

const ContentContainer = styled('div')`
  ${contentContainerStyles}
`;

const headerStyles = ({ theme }) => css`
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 3rem;
  color: ${theme.colors.p600};
`;

const Header = styled('h1')`
  ${headerStyles}
`;

const titleStyles = ({ theme }) => css`
  margin-top: 0;
  color: ${theme.colors.p400};

  ${theme.mq.huge`
    font-size: 2.5rem;
  `};
`;

const Title = styled('h2')`
  ${titleStyles}
`;

const imageStyles = css`
  left: 0;
  top: 0;
  margin-bottom: 20px;
`;

const Image = styled(GatsbyImage)`
  ${imageStyles}
`;

const textStyles = ({ theme }) => css`
  color: ${theme.colors.textColor};
  ${theme.mq.huge`
     width: 80%;
  `};
`;

const Text = styled('p')`
  ${textStyles}
`;

const AboutPage = ({ location, data }) => {
  const { avatar, contentfulPage } = data;
  const { title, subTitle, paragraph, metadata } = contentfulPage;

  return (
    <Container>
      <SEO title={metadata.title} keywords={[metadata.keywords]} />
      <HeaderContainer>
        <Header>{title}</Header>
        <Title>{subTitle}</Title>
      </HeaderContainer>
      <ContentContainer>
        <Image fluid={avatar.childImageSharp.fluid} alt="Author image" />
        <Text>{paragraph.paragraph}</Text>
      </ContentContainer>
    </Container>
  );
};

AboutPage.propTypes = {
  data: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
};

export default AboutPage;

export const pageQuery = graphql`
  query {
    contentfulPage(name: { eq: "About Page" }) {
      title
      subTitle
      paragraph {
        paragraph
      }
      metadata {
        title
        keywords
      }
    }
    avatar: file(absolutePath: { regex: "/guy-about.jpg/" }) {
      childImageSharp {
        fluid(maxWidth: 1000, maxHeight: 300, cropFocus: CENTER) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
